import React, { useEffect, useState } from 'react';
import SojiBaseInit from '@modules/SojiBase';
import Preloader from '@components/preloader';

function isValidHttpUrl(string) {
    var res = string.match(/(http(s)?:\/\/.)?(www\.)?[-a-zA-Z0-9@:%._\+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_\+.~#?&//=]*)/g);
    return (res !== null)
  };

export default function RequestAction({ state, stateHandler, onRequestClose }) {
    console.log(state.links)
    useEffect(() => {
        (async () => {
            stateHandler('noInfo', false);
            stateHandler('response', false);
            if (!isValidHttpUrl(state.links)) {
                stateHandler('noInfo', 'Please enter valid Link');
                onRequestClose();
                return;
            }
            if (state.links == '') {
                stateHandler('noInfo', "Link textbox can't be empty");
                onRequestClose();
                return;
            }
            const linkToSearch = state.links.split('?')[0]
            stateHandler('onRequest', true);
            const SojiBase = await SojiBaseInit;
            const requestPayload1 = { links: linkToSearch };
            const getter = await SojiBase.post('v1/search_engine/search_links', requestPayload1);
            getter.on('static', async (response) => {
                stateHandler('onRequest', false);
                const data = response.response.data;
                if (data.length > 0) {
                    stateHandler('response', data);
                    onRequestClose();
                } else {
                    stateHandler('noInfo', 'No Information Yet');
                    onRequestClose();
                }
            });
        })();
    }, []);

    return <>{state.onRequest && <Preloader />}</>;
}
